@import url('https://fonts.googleapis.com/css?family=Lato:400,700');

html {
  font-size: 15px;
}
body {
  display: -ms-flexbox;
  display: flex;
  font-family: 'Lato', sans-serif;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}

.heading {
  font-size: 5rem;
  color: #777;
}

@media screen and (max-width: 768px) {
  html {
    font-size: 11px;
  }
}



